<template>
  <div>
    <router-link to="/coins/bitcoin">
      <button id="myButton" class="submit">Bitcoin</button>
    </router-link>
    <p>Hello</p>
    <p>Name: {{ coin.name }}</p>
    <p>Symbol: {{ coin.symbol }}</p>
    <p>Price (USD): {{ coin.price_usd }}</p>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Coins',

  data () {
    return {
      coin: {}
    }
  },

  created () {
    console.log('coins')
    this.fetchData()
  },

  watch: {
    $route: 'fetchData'
  },

  methods: {
    fetchData () {
      axios
        .get(
          'https://api.coinmarketcap.com/v1/ticker/' + this.$route.params.id + '/'
        )
        .then(resp => {
          this.coin = resp.data[0]
          console.log(resp)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>



