<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <img v-bind:src= imgSrc style="display:block; margin:auto">
  </div>
</template>

<script>
import logo from '../assets/logo.png'

export default {
  name: 'Hello',
  data () {
    return {
      msg: 'Welcome Max',
      imgSrc: logo
    }
  }
}
</script>



